import React, { Component } from 'react';
import {render} from 'react-dom';

import 'static/others/mtl-core.js'
const appId = 'summerMaDemo';
mtl.ucgAction.writeUCGConfig(appId,{
  host: 'ma-gateway.test.app.yyuap.com',
  ishttps: false,
  default_tp: 'none'
})

class App extends Component{
  constructor(props) {
    super(props)
    this.state = {
      user: '',
      pass: ''
    }
  }
  login = () => {
    const { user, pass } = this.state;
    mtl.ucgAction.callService({
      appId: appId,
      params: {
        user,
        pass
      }

    }, function (res) {
      debugger
    })
  }
  render() {
    return (
      <div>
        <div>
          <input
            type="text"
            onChange={e => {
              this.setState({user: e.target.value})
            }}
          />
        </div>
        <div>
          <input
            type="password"
            onChange={e => {
              this.setState({pass: e.target.value})
            }}
          />
        </div>
        <div>
          <button onClick={this.login}></button>
        </div>
      </div>
    )
  }
}

render(<App/>, document.getElementById('app'))